.conditions {
    .fullSizedList {
        @apply mobile:mt-0;
        .listItem {
            @apply mobile:flex mobile:items-center mobile:max-w-none mobile:min-h-0 mobile:text-left mobile:w-full mobileXs:w-[208px] mobileXs:mx-auto mobileXs:max-w-[208px] mobileXs:mb-0 mobile:first-of-type:mt-0;

            & + .listItem {
                @apply mobile:mt-2;
            }

            svg {
                @apply mobile:mx-0;
            }

            h4 {
                @apply mobile:-mb-2;
            }
        }
    }
}

.fullSizedList {
    @apply flex mobile:pt-[40px] mobile:mx-auto mobile:max-w-[450px] flex-wrap -mt-6 mobile:-mt-4 mobile:pb-10;

    .listItem {
        @apply w-full max-w-[440px] notMobile:flex notMobile:flex-row mobile:max-w-[156px] mobile:min-h-[150px] bg-white mobile:text-center rounded-lg shadow-popUp py-2 mobile:py-1 px-4 mobile:px-2 whitespace-normal align-middle mobile:w-1/2 mobileXs:w-[208px] mobileXs:mx-auto mobileXs:max-w-[208px] mobileXs:mb-0 mt-6 mobile:mt-4 notMobile:ml-auto;

        & + .listItem {
            @apply mobileXs:ml-auto mobileXs:mt-4 mobile:ml-0;
        }

        &:nth-child(2n) {
            @apply mobile:ml-auto;
        }

        svg {
            @apply notMobile:mr-4 min-w-[60px] mobile:min-w-[48px] mobile:h-[48px] mobile:mx-auto;
        }

        h4 {
            @apply mt-[14px] mobile:mt-0 text-2xl mobile:text-[14px] text-[#1E282D] font-semibold mobile:leading-5;
        }

        span {
            @apply text-[18px] mobile:text-[10px] leading-[27px] mobile:leading-[15px] text-[#1E282D] inline-block;
        }
    }
}

.list {
    @apply flex relative mobile:pt-[40px] mobile:mx-auto mobile:max-w-[400px] flex-wrap -mt-6 mobile:-mt-4;

    .listItem {
        @apply w-[208px] max-w-[208px] mobile:max-w-[47%] min-h-[162px] bg-white text-center rounded-lg shadow-popUp p-6 whitespace-normal align-middle mobile:w-1/2 mobileXs:w-[208px] mobileXs:mx-auto mobileXs:max-w-[208px] mobileXs:mb-0 mt-6 mobile:mt-4;

        & + .listItem {
            @apply ml-6 mobileXs:ml-auto mobileXs:mt-4 mobile:ml-0;
        }

        &:nth-child(2n-1) {
            @apply notMobile:ml-auto;
        }

        &:nth-child(2n) {
            @apply mobile:ml-auto;
        }

        svg {
            @apply mb-2 mx-auto;
        }

        h4,
        span {
            @apply text-[#1E282D] static;
        }

        h4 {
            @apply font-semibold;
        }

        span {
            @apply text-[10px] inline-block font-medium;
        }
    }
}
